// Базовые цвета сайта

$red: #d2393e;
$yellow: #ffd200;
$yellow-mutted: #ffc300;
$black: #000;
$gray-dark: #555;
$gray: #666666;
$gray-mutted: #999;
$gray-soft: #cdcdcd;
$gray-light :#7C8992;
$white : #fff;
$link-hover-color: #c00;
$blue: #384B5A;
$light-blue: #358CDA;
$bg-link-color: #6c8598;

// Семантические цвета

$bg-aside-color: $blue;
$border-menu: $gray-light;

$text-color: $gray-dark;
$text-color--muted: $gray-mutted;
$body-bg: #fff;

$link-color: $black;
$link-color--hover: $red;

$border-color: $gray-light;

$border-color: #e7e7e7;

// Базовая типографика

$fz-text: 2.3em;

$font-size: 100%;
$font-size--h1: 5.5em;
$font-size--h2: 5em;
$font-size--h3: 4.2em;
$font-size--h4: 3.6em;
$font-size--h5: 3.2em;
$font-size--h6: 3.2em;

$line-height: 1.3em;

$font-family: -apple-system, BlinkMacSystemFont, 'Roboto', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', 'Arial', sans-serif;
$font-family--serif: 'Georgia', 'Times New Roman', 'Times', serif;
$font-family--monospace: 'Menlo', 'Monaco', 'Consolas', 'Liberation Mono', 'Courier New', monospace;
$font-family--headings: $font-family;

// Разное

$field-padding-vertical: 0.3em;
$field-padding-horizontal: 0.7em;

$opacity: 0.7;
$transition-time: 0.3s;
$shadow: 0 4px 2px -2px rgba(0, 0, 0, 0.3);

$toggler-part-height: 3px;
$toggler-part-bg-color: #000;

$z-index-modal: 100;
