// В этом файле должны быть стили для БЭМ-блока phone-book, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.phone-book {
	width: 100%;
	height: 100%;

	&__title {
	    overflow: hidden;
	    padding: 4em;
	    background-color: white;
	    position: fixed;
	    top: 0;
	    width: 100%;
	    height: 25em;
	    left: 0;
	    align-items: center;
	    z-index: 9;
	}

	.workers-block {
		margin-top: 25em;
		padding: 0 4em;
		flex-flow: row wrap;

		.worker {
			flex-basis: 33%;
			border-bottom: 1px solid $gray-light;
			padding: 1.5em 2em 2em 0;
			height: 30em;

			&__img {
				flex-flow: column nowrap;

				img {
					width: 15em;
					height: auto;
				}

				a {
					color: $black;
				    font-size: $fz-text;
				    position: relative;
				    border: 1px solid $black;
				    border-radius: 3px;
				    line-height: 1.7;
				    padding-left: 1.2em;
				    margin-top: 0.5em;

					&::before {
						content: "";
						display: block;
						width: 1em;
						height: 1em;
						position: absolute;
					}

					&:hover {
						background-color: transparent;
					}
				}
			}

			&__description {
				font-size: $fz-text;
				margin-left: 1em;

				span {
					display: inline-block;
				}

				a {
					color: $black;
					display: block;

					&:hover {
						background-color: transparent;
					}
				}

				&-name {
					font-size: 130%;
					font-weight: bold;
					margin-bottom: 0.5em;
				}
			}
		}
	}
}

