body {
  background: $body-bg;
  color: $black;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.page {
	width: 100%;
	height: 100%;
}

.hidden {
  display: none;
}

.flex {
	display: flex;
}

h1 {
	font-size: $font-size--h1;
	margin: 1.5em 0;
}

h2 {
	font-size: $font-size--h2;
	margin: 1.3em 0;
}

h3 {
	font-size: $font-size--h3;
	margin: 1em 0;
}

h4 {
	font-size: $font-size--h4;
	margin: 1em 0;
}

h5,
h6 {
	font-size: $font-size--h5;
	margin: 1em 0;
}

a:hover,
a:focus,
a:active {
	background-color: #6c8598;
}

@media (max-width: 3870px) {

	body {
		font-size: 14px;
	}
}
@media (max-width: 3440px) {

	body {
		font-size: 12px;
	}
}
@media (max-width: 2900px) {

	body {
		font-size: 9px;
	}
}
@media (max-width: 2560px) {

    body {
        font-size: 8px;
    }
}
@media (max-width: 2000px) {

    body {
        font-size: 7px;
    }
}
@media (max-width: 1690px) {

	body {
		font-size: 6px;
	}
}
@media (max-width: 1440px) {
    body {
        font-size: 4.5px;
    }
}
@media (max-width: 1024px) {

    body {
        font-size: 3px;
    }
}
@media (max-width: 768px) {

    body {
        font-size: 3px !important;
    }
}
