// В этом файле должны быть стили для БЭМ-блока main-block, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.main-block {
	width: 100%;
	height: 100%;
	overflow: auto;
	padding-left: 5em;
	padding-right: 15em;
	position: relative;

	&__directors {

		p {
			font-size: $fz-text;
		}

		.directors {

			&__list {
				flex-flow: row wrap;

				&-item {
					flex-basis: 33%;
					min-height: 27em;
					padding-left: 5em;

					&:nth-child(1),
					&:nth-child(4),
					&:nth-child(7) {
						padding: 0;
					}

					&-name {
						font-size: 3em;
						font-weight: bold;
						color: $light-blue;
						line-height: 1.5;
					}

					&-description {
						padding: 1em 0;
						font-size: $fz-text;
						margin: 0;
					}
				}
			}
		}
	}

	&__releases {

		.releases__content {
		    flex-flow: column nowrap;

			&-block {
				flex-flow: row nowrap;
				padding: 2em 0;
				border-top: 1px solid $gray-light;
				font-size: $fz-text;
				line-height: 1.3;

				p {
					margin: 0;
				}

				&-date {
					padding: 0 2em;
					color: initial;
				}

				&:last-child {
					border-bottom: 1px solid $gray-light;
				}
			}
		}

		.releases__tabs {
		    position: fixed;
		    overflow: hidden;
		    bottom: 20em;
		    width: 55%;
		    height: 25em;
		    align-items: center;
		    background-color: $white;

			&-item {
				display: block;
				font-size: $fz-text;
				border: 1px solid $gray-light;
				color: $black;

				&:hover,
				&:active,
				&:focus {
					border: 1px solid $bg-link-color;
					color: $white;
				}

				span {
					padding: 0.7em;
					display: inline-block;
				}

				&.tabs-active {
					color: $white;
					background-color: $bg-link-color;
					border: 1px solid $bg-link-color;
				}
			}
		}
	}

	&__callback {

		details {
			display: block;
			font-size: $fz-text;
			padding: 1.5em 1em;
			border-top: 1px solid $gray-light;

			&[open] {
				background-color: #EEF2F5;

				summary {
					margin-bottom: 1rem;
				}
			}

			&:last-child {
				border-bottom: 1px solid $gray-light;
			}

			summary {
				display: block;
				color: $light-blue;

				&:focus {
					outline: none;
				}

				&::-webkit-details-marker {
					display: none;
				}

				&::before {
					content: '';
				}
			}

			p {
				padding-left: 1em;
			}
		}
	}

	&__article {

		p,
		a,
		span {
			font-size: $fz-text;
		}
	}

	&__search {

		.search__block {
			flex-flow: row nowrap;
			border-top: 1px solid $gray-light;
			font-size: $fz-text;

			&:last-child {
				border-bottom: 1px solid $gray-light;
			}

			&-categories {
				flex-basis: 18%;
				padding: 1em 0;
			}

			&-link {
				padding: 1em 0 1em 1em;
			    overflow: hidden;

				&-text {
					color: $black;
				    overflow: hidden;
				    text-overflow: ellipsis;
				    white-space: nowrap;
				}
			}
		}
	}

	&__news {

		.news__block {

			&:last-child:not(:first-child) {
				padding-bottom: 20em;
			}

			&-container {

			}

			&-link {
				display: block;
				display: flex;
				flex-direction: column;

				&:not(:first-child) {
					margin-left: 10em;
				}

				&:hover,
				&:active,
				&:focus {
					background-color: $white;
					color: $light-blue;
				}

				img {
					width: 25em;
					height: auto;
				}

				span {
					font-size: $fz-text;
					padding-top: 1em;
				}
			}
		}

		.news__tabs {
	    	position: fixed;
		    bottom: 20em;
		    width: 55%;
		    height: 16em;
		    overflow: hidden;

		    &-fixed {
			    width: 55%;
			    height: 25em;
			    background: -moz-linear-gradient(top,  rgba(255,255,255,0) 0%, rgba(255,255,255,0.57) 27%, rgba(255,255,255,1) 47%, rgba(255,255,255,1) 100%);
				background: -webkit-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(255,255,255,0.57) 27%,rgba(255,255,255,1) 47%,rgba(255,255,255,1) 100%);
				background: linear-gradient(to bottom,  rgba(255,255,255,0) 0%,rgba(255,255,255,0.57) 27%,rgba(255,255,255,1) 47%,rgba(255,255,255,1) 100%);
				filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=0 );
			    opacity: 0.8;
			    position: fixed;
			    pointer-events: none;
			    cursor: default;
			    bottom: 36em;
		    }

		    &-container {
		    	width: 100%;
		    	height: 100%;
			    align-items: flex-start;
			    background-color: $white;

				a {
					display: block;
					font-size: $fz-text;
					border: 1px solid $gray-light;
					color: $black;

					&:hover,
					&:active,
					&:focus {
						border: 1px solid $bg-link-color;
						color: $white;
					}

					span {
						padding: 1em;
						display: inline-block;
					}

					&.tabs-active {
						color: $white;
						background-color: $bg-link-color;
						border: 1px solid $bg-link-color;
					}
				}
		    }
		}
	}

	&__photogallery {
		flex-flow: row wrap;

		.photogallery__item {
			flex-basis: 20%;
			display: block;
		    margin: 0 5em 4em 0;

			&:hover,
			&:active,
			&:focus {
				background-color: $white;
				color: $light-blue;
			}

			img {
				width: 100%;
				height: auto;
			}

			span {
				font-size: $fz-text;
				padding-top: 0.5em;
				display: inline-block;
			}
		}
	}
}
