// В этом файле должны быть стили для БЭМ-блока bottom-aside, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.bottom-aside {
	background-color: $bg-aside-color;
	position: fixed;
	z-index: 10;
	bottom: 0;
	left: 0;
	height: 20em;
    width: calc(100% - 35em);
    flex-flow: row nowrap;

    &__block {

		.block__language {
			flex-direction: column;
			height: 100%;


			a {
				height: 50%;
				display: flex;
				align-items: center;
				padding: 0 1em;
				color: $white;

				span {
					text-transform: uppercase;
					font-size: 2em;
				}
			}
		}

		.block__widget {
			color: $white;
			padding: 1.5em;
			flex-direction: column;
			justify-content: center;

			&-time {

				span {
					font-size: 10em;
				}
			}

			&-date {

				span {
				    font-size: 2.1em;
				    font-variant: small-caps;
				}
			}
		}

		.block__form {
			font-size: 2em;
			padding: 0 1em;
			align-items: center;

			a {
				justify-content: center;
				align-items: center;
				width: 5em;

				img {
					width: 3em;
					height: auto;
				}
			}

			input {
				height: 30%;
				font-size: 1.8em;
			    width: 60%;
			    background-color: transparent;
			    border: none;
			    border-bottom: 1px solid white;
			    color: $white;

			    &:focus {
			    	outline: none;
			    }
			}

			button {
			    margin-left: 0.7em;
			    cursor: pointer;
			    outline: none;
			    border: 1px solid white;
			    background-color: transparent;
			    color: white;
			    font-size: 1.8em;
			    height: 30%;
			    border-radius: 10%;

			}
		}
    }

    &__menu {

		.menu__list {

			&-item {

				&-link {
					display: flex;
					flex-direction: row;
					align-items: center;
					justify-content: flex-start;
					text-align: left;
					height: 100%;

					img {
					    width: 6em;
					    height: auto;
					    margin: 0 1em;
					}

					span {
						font-size: 2.5em;
						color: $white;
					}
				}
			}
		}
    }

    .menu__btn-more {
    	width: 10%;

    	a {
    		width: 100%;
    		height: 100%;
			align-items: center;
			justify-content: center;

    		img {
			    width: 6em;
			    height: auto;
    		}
    	}
    }
}

.block__language,
.block__widget,
.block__form,
.menu__list-item {
	border-right: 1px solid $border-menu;
}

