// В этом файле должны быть стили для БЭМ-блока slider, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

/* Slider */
.slick-slider
{
    position: relative;

    display: block;
    box-sizing: border-box;

    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
        touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list
{
    position: relative;

    display: block;
    overflow: hidden;

    margin: 0;
    padding: 0;
}
.slick-list:focus
{
    outline: none;
}
.slick-list.dragging
{
    cursor: pointer;
    cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list
{
    -webkit-transform: translate3d(0, 0, 0);
       -moz-transform: translate3d(0, 0, 0);
        -ms-transform: translate3d(0, 0, 0);
         -o-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.slick-track
{
    position: relative;
    top: 0;
    left: 0;

    display: block;
    margin-left: auto;
    margin-right: auto;
}
.slick-track:before,
.slick-track:after
{
    display: table;

    content: '';
}
.slick-track:after
{
    clear: both;
}
.slick-loading .slick-track
{
    visibility: hidden;
}

.slick-slide
{
    display: none;
    float: left;

    height: 100%;
    min-height: 1px;
}
[dir='rtl'] .slick-slide
{
    float: right;
}
.slick-slide img
{
    display: block;
}
.slick-slide.slick-loading img
{
    display: none;
}
.slick-slide.dragging img
{
    pointer-events: none;
}
.slick-initialized .slick-slide
{
    display: block;
}
.slick-loading .slick-slide
{
    visibility: hidden;
}
.slick-vertical .slick-slide
{
    display: block;

    height: auto;

    border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
    display: none;
}

/*  end styles for slider */

.slider-container {
	position: absolute;
	top: 0;
	left: 0;
	display: none;

	&.active-slider {
		display: block;

		.slider.slick-slider {
		    width: 100vw;
		    height: 100vh;
		    z-index: 20;
		    background-color: $white;
		    display: flex;
		    align-items: center;

		    div {
		    	width: 100%;
		    	height: 100%;
		    }
		}

		.slider__navigation-panel {
			display: flex;
			justify-content: center;
			position: absolute;
			z-index: 20;
			bottom: 0;
			left: 0;
			width: 100vw;
			height: 20em;
		    background-color: rgba(128, 128, 128, 0.5);
		    /* IE9, iOS 3.2+ */
		    background-image: url(data:image/svg+xml;base64,);
		    background-image: -webkit-gradient(linear, 0% 0%, 0% 100%,color-stop(0, rgb(255, 255, 255)),color-stop(1, rgba(0, 0, 0, 0)));
		    /* Android 2.3 */
		    background-image: -webkit-linear-gradient(top,rgb(255, 255, 255) 0%,rgba(0, 0, 0, 0) 100%);
		    /* IE10+ */
		    background-image: linear-gradient(to bottom,rgb(255, 255, 255) 0%,rgba(0, 0, 0, 0) 100%);
		    background-image: -ms-linear-gradient(top,rgb(255, 255, 255) 0%,rgba(0, 0, 0, 0) 100%);


			ul {
				align-items: center;
				justify-content: center;

				li {
					margin-left: 5em;

					a {
						display: block;

						&:hover,
						&:active,
						&:focus {
							background-color: transparent;
						}

						img {
							width: 15em;
						}
					}
				}
			}
		}
	}

	.slider {
		display: none;

		img {
			width: 100%;
			height: 100%;
		}
	}

	.slider__navigation-panel {
		display: none;
	}
}
