// В этом файле должны быть стили для БЭМ-блока right-aside, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.right-aside {
	background-color: $bg-aside-color;
	position: fixed;
	z-index: 10;
	right: 0;
	top: 0;
	height: 100%;
	width: 35em;
	flex-direction: column;
	justify-content: space-between;

	&__logo {
		height: 17em;

		a {
			display: block;
			padding: 3em;

			&:hover,
			&:active,
			&:focus {
				background-color: transparent;
			}

			img {
				height: 100%;
				width: 100%;
			}
		}
	}

	&__navigation {
		width: 100%;

		.navigation__list {
			height: 100%;
			flex-flow: column nowrap;
			justify-content: flex-end;

			&-item {
				line-height: 6em;
				border-top: 1px solid $border-menu;
				position: relative;

				.sub-navigation {
					display: none;
				}

				&:hover .sub-navigation {
					display: block;
					position: absolute;
					background-color: $bg-aside-color;
				    transform: translateX(-100%);
				    padding: 2em;
				    bottom: 0;
				    color: $white;
				    min-height: 55em;
				    height: 100%;

					&__title {
						font-size: 2.8em;
					}

					&__list {
						flex-direction: column;

						li {

							a {
								font-size: 2.5em;
								display: block;
								padding: 0 0.5em;
								color: $white;
							}
						}
					}
				}

				&-link {
					display: flex;
					flex-direction: row;
					align-items: center;
					justify-content: flex-start;
					text-align: left;

					img {
					    width: 5em;
					    height: auto;
					    margin: 0 2em;
					}

					span {
						color: $white;
					    font-size: 2.5em;
					}
				}
			}
		}
	}
}
