// В этом файле должны быть стили для БЭМ-блока content, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.content {
	width: calc(100% - 35em);
	height: calc(100% - 20em);
	overflow: auto;
	display: flex;
	flex-flow: row nowrap;

	&__bg-block {
		width: 35%;
		height: 100%;
		border-right: 1px solid $gray-light;
		background-color: $gray-light;

		img {
			width: 100%;
			height: 100%;
		}
	}

	&.news {

		.content__bg-block {
			background-color: $white;
			border: none;
		    overflow: hidden;
		    flex-direction: column;
		    justify-content: flex-end;

			.slider-newspaper-container {
				padding: 0 0 2em 2em;
				font-size: 2.6em;

				.slider-newspaper {
					pointer-events: none;

					div {
						width: 100%;
						height: 100%;
						max-height: 60%;

						img {
							width: 100%;
							height: 100%;
						}
					}
				}

				.slider-newspaper-nav {
					justify-content: space-between;
					margin-top: 1.5em;

					a:hover,
					a:active,
					a:focus {
						background-color: $white;
						color: $light-blue;
					}

					.newspaper-nav__arrows {

						.newspaper-nav__page {
							margin: 0 1.5em;
							display: inline-block;
						}

						.prev-slide,
						.next-slide {
							width: 1.5em;
							height: 1.5em;
							position: relative;
							cursor: pointer;

							&::before,
							&::after {
								content: '';
							    width: 1em;
							    position: absolute;
							    border: 1px solid $gray-light;
								display: block;
							}
						}

						.prev-slide {

							&::before{
								content: '';
							    top: 0;
							    transform: rotate(-45deg);
							}

							&::after {
								content: '';
							    top: 50%;
							    transform: rotate(45deg) translateY(-50%);
							}
						}

						.next-slide {

							&::before{
								content: '';
							    top: 0;
							    transform: rotate(45deg);
							}

							&::after {
								content: '';
							    top: 50%;
							    transform: rotate(-45deg) translateY(-50%);
							}
						}
					}
				}
			}
		}
	}

	&__main-block {
		width: 65%;
		height: 100%;
	}

	&__main {
		width: 100%;
		height: 100%;
		padding-left: 5em;
	}

	.callback__button  {
		position: absolute;
		bottom: 30em;
		left: 10em;
		background-color: $bg-aside-color;
		border: none;
		cursor: pointer;

		&:hover,
		&:active,
		&:focus {
			outline: none;
		}

		span {
			padding: 0 1em 0 2.5em;
			font-size: $fz-text;
			line-height: 2.3em;
			position: relative;
			color: white;

			&::before {
				content: "";
				display: block;
				position: absolute;
				margin-right: 0.5em;
				width: 1em;
				height: 1em;
				background: url(../img/icon.svg) no-repeat center center;
				background-size: 100%;
			    top: 50%;
			    transform: translateY(-50%);
			    left: 8%;
			}
		}
	}
}
